@import '~assets/styles/h3Imports';

.banner {
  background-color: var(--onboarding-banner-background-color);
  border-radius: var(--onboarding-banner-border-radius, 16px);
  color: var(--onboarding-banner-time-read-color);
  display: flex;
  justify-content: flex-end;
  max-width: var(--onboarding-banner-max-width);
  min-height: var(--onboarding-banner-min-height, 344px);
  overflow: hidden;
}

.buttons {
  position: relative;
  z-index: 2;
}

.banner-content {
  display: flex;
  flex: 0 1 50%;
  flex-direction: column;
  justify-content: center;
  padding: 38px 56px;
  width: 50%;

  --button-min-width: none;

  a {
    font-size: rem(14);
    height: rem(40);
    line-height: line-height(20, 14);
    margin: 0;
    margin-top: rem(16);
    min-width: rem(263);
    padding: rem(10) rem(16);
    z-index: var(--onboarding-banner-z-index-content, 1);
  }
}

.time-read {
  @include caption-text;

  z-index: var(--onboarding-banner-z-index-content, 1);
}

.banner-title {
  font-size: rem(27);
  line-height: 1.18;
  margin: 0;
  z-index: var(--onboarding-banner-z-index-content, 1);

  @media (max-width: 400px) {
    [data-fontsize='AAA'] & {
      font-size: rem(25);
    }
  }
}

.banner-description {
  @include paragraph;

  margin: em(16) 0 0;
  position: relative;
  z-index: var(--onboarding-banner-z-index-content, 2);
}

.illustration-mobile {
  display: none;
}

.illustration {
  align-items: center;
  display: flex;
  flex: 0 1 50%;
  justify-content: center;
  overflow: visible;
  pointer-events: none;
  position: relative;
  width: 50%;

  img {
    margin-top: auto;
    min-height: rem(360);
    object-fit: cover;

    // min-width: 507px;
    z-index: var(--onboarding-banner-z-index-img, 1);

    [data-fontsize='AA'] &,
    [data-fontsize='AAA'] & {
      min-height: 100%;
      object-fit: initial;
    }
  }
}

@media #{$large-down} {
  .banner {
    min-height: rem(344);
  }

  .banner-content {
    padding: 34px 32px;

    a {
      margin-top: rem(8);
    }
  }

  .illustration {
    height: rem(344);

    img {
      margin-top: -0.5rem; // -8px;
      // min-width: rem(590);
    }
  }
}
@mixin small-down {
  .banner {
    flex-direction: column;
  }

  .illustration {
    display: none;
  }

  .banner-content {
    padding: 32px 16px 8px;
    width: 100%;

    a {
      margin: 0;
      min-width: 100%;
    }
  }

  .illustration-mobile {
    align-items: center;
    display: flex;
    height: rem(264);
    justify-content: center;
    margin-top: rem(16);
    overflow-x: visible;
    overflow-y: clip;
    pointer-events: none;

    img {
      margin-left: rem(44);
      margin-top: -1.5rem; //-24px;
      min-width: 510px;
      z-index: var(--onboarding-banner-z-index-img, 1);
    }
  }
}

@media #{$small-down} {
  @include small-down;
}

[data-fontsize='AAA'] {
  @media #{$large-up} {
    @include small-down;

    .banner-content {
      padding: 38px 56px;
    }

    .illustration-mobile {
      height: auto;
    }
  }

  @media #{$small-down} {
    .illustration-mobile {
      height: auto;
    }
  }
}

@media (min-width: 1150px)  and (max-width: #{$mq-xlarge-1}) {
  .illustration {
    justify-content: right;
  }
}
